import React, { Component } from 'react';

import './cash_collection.scss';
import axios from 'axios';

class cash_collection extends Component {

	state = {
		cash_data: [],
	};

	componentWillMount() {
		axios
			.get('http://stage.medicento.com:8080/orders/get_cash_status/')
			.then((response) => {
				console.log(response)
				this.setState({
					cash_data: response.data.data,
				});
			})
			.catch((err) => {
				console.log(err);
			});
	}

	render() {

		const cash_data = this.state.cash_data;

		return (
			<div className="cash_collection">
				<div className="row">
					<h3>Pharmacy Collection</h3>
					<div className="col-md-12 select_pharmacy">
						<table className="table table-bordered">
							<thead>
								<tr>
									<th>Pharmacy Name</th>
									<th>Collected At</th>
									<th>Amount Collected</th>
									<th>Collected By</th>
									<th>Payment Mode</th>
								</tr>
							</thead>
							<tbody>
								{
									cash_data.map((each) => (
										<tr>
											<td>{each.pharmacy}</td>
											<td>{each.created_at}</td>
											<td>{each.amount_recived}</td>
											<td>{each.collected_by}</td>
											<td>{each.collection_type}</td>
									</tr>
								))}
							</tbody>
						</table>
					</div>
				</div>
			</div>
		);
	}
}

export default cash_collection;
