import React, { Component } from 'react';

import './pharmacy_profile.scss';
import axios from 'axios';

import { Link } from 'react-router-dom';
export default class pharmacy_profile extends Component {

	state = {
		pharmas: [],
		temp: [],
		most_seeling: [],
		lowest_selling: [],
		never_selling: [],
		ascending_month: true,
		ascending_week: true,
		ascending_most_times: true,
		ascending_most_overall: true,
		ascending_lowest_times: true,
		ascending_lowest_overall: true,
		ascending_never_times: true,
		ascending_never_overall: true,
	};

	componentWillMount() {
		axios
			.get('http://stage.medicento.com:8080/pharmacy/get_pharmacy_sales/')
			.then((response) => {
				console.log(response);
				this.setState({
					pharmas: response.data.data,
					temp: response.data.data,
					most_seeling: response.data.most_seeling,
					lowest_selling: response.data.lowest_selling,
					never_selling: response.data.never_selling
				});
			})
			.catch((err) => {
				console.log(err);
			});
	}


	searchCity = (event) => {
		event.preventDefault();
		let term = event.target.value;
		let pharmas = this.state.pharmas;
		console.log(term, this.state.temp[0].state)
		if (term.length > 0) {
			pharmas = this.state.temp.filter(obj => obj.state.toLowerCase().indexOf(term.toLowerCase()) != -1)	
		} else {
			pharmas = this.state.temp;
		}
		this.setState({
			pharmas: pharmas,
		});
	}

	searchPharmacy = (event) => {
		event.preventDefault();
		let term = event.target.value;
		let pharmas = this.state.pharmas;
		console.log(term, this.state.temp[0].state)
		if (term.length > 0) {
			pharmas = this.state.temp.filter(obj => obj.name.toLowerCase().indexOf(term.toLowerCase()) != -1)	
		} else {
			pharmas = this.state.temp;
		}
		this.setState({
			pharmas: pharmas,
		});
	}

	ascendingMonth = () => {
		let pharmas = this.state.pharmas;
		pharmas.sort((a, b) => a.monthly - b.monthly);
		if (!this.state.ascending_month) {
			pharmas=pharmas.reverse();	
		} 
		this.setState({
			pharmas: pharmas,
			ascending_month: !this.state.ascending_month
		});
	};

	ascendingWeek = () => {
		let pharmas = this.state.pharmas;
		pharmas.sort((a, b) => a.weekly - b.weekly);
		if (!this.state.ascending_week) {
			pharmas=pharmas.reverse();	
		} 
		this.setState({
			pharmas: pharmas,
			ascending_week: !this.state.ascending_week
		});
	};

	ascendingtimes_ordered = () => {
		let most_seeling = this.state.most_seeling;
		most_seeling.sort((a, b) => a.times_ordered - b.times_ordered);
		if (!this.state.ascending_most_times) {
			most_seeling=most_seeling.reverse();	
		} 
		this.setState({
			most_seeling: most_seeling,
			ascending_most_times: !this.state.ascending_most_times
		});
	};

	ascendingoverall_qty = () => {
		let most_seeling = this.state.most_seeling;
		most_seeling.sort((a, b) => a.overall_qty - b.overall_qty);
		if (!this.state.ascending_most_overall) {
			most_seeling=most_seeling.reverse();	
		} 
		this.setState({
			most_seeling: most_seeling,
			ascending_most_overall: !this.state.ascending_most_overall
		});
	};

	ascendingtimes_ordered_lowest = () => {
		let lowest_selling = this.state.lowest_selling;
		lowest_selling.sort((a, b) => a.times_ordered - b.times_ordered);
		if (!this.state.ascending_lowest_times) {
			lowest_selling=lowest_selling.reverse();	
		} 
		this.setState({
			lowest_selling: lowest_selling,
			ascending_lowest_times: !this.state.ascending_lowest_times
		});
	};

	ascendingoverall_qty_lowest = () => {
		let lowest_selling = this.state.lowest_selling;
		lowest_selling.sort((a, b) => a.overall_qty - b.overall_qty);
		if (!this.state.ascending_lowest_overall) {
			lowest_selling=lowest_selling.reverse();	
		} 
		this.setState({
			lowest_selling: lowest_selling,
			ascending_lowest_overall: !this.state.ascending_lowest_overall
		});
	};


	render() {
		const pharmas = this.state.pharmas;
		const most_seeling = this.state.most_seeling;
		const lowest_selling = this.state.lowest_selling;
		const never_selling = this.state.never_selling;

		return (
			<div className="pharmacy_profile">
				<div className="search">
					<input type="text" name="city" placeholder="City" onChange={this.searchCity} />
					<input type="text" name="pharmacy" placeholder="Pharmacy" onChange={this.searchPharmacy} />
				</div>
				<div className="row">
					<div className="col-md-4">
						<h5>Pharmacy Sales</h5>
						<div className="box_pharma">
							<table className="table table-bordered">
								<thead>
									<tr>
										<th>Name</th>
										<th onClick={this.ascendingMonth}>Monthly {this.state.ascending_month ? <i class="fas fa-chevron-up"></i> : <i class="fas fa-chevron-down"></i>}</th>
										<th onClick={this.ascendingWeek}>Weekly {this.state.ascending_week ? <i class="fas fa-chevron-up"></i> : <i class="fas fa-chevron-down"></i>}</th>
									</tr>
								</thead>
								<tbody>
									{pharmas.map((pharma) => (
										<tr>
											<td><Link to={'/pharmacy_data/' + pharma.id + '/'}>{pharma.name}</Link></td>
											<td>Rs.{pharma.monthly.toLocaleString(navigator.language, { minimumFractionDigits: 0 })}</td>
											<td>Rs.{pharma.weekly.toLocaleString(navigator.language, { minimumFractionDigits: 0 })}</td>
										</tr>
									))}
								</tbody>
							</table>
						</div>
					</div>
					<div className="col-md-4 pl-0 pr-0">
						<h5>Most Selling Medicine</h5>
						<div className="box_selling">
							<table className="table table-bordered">
								<thead>
									<tr>
										<th>Name</th>
										<th onClick={this.ascendingtimes_ordered}>Times Ordered {this.state.ascending_most_times ? <i class="fas fa-chevron-up"></i> : <i class="fas fa-chevron-down"></i>}</th>
										<th onClick={this.ascendingoverall_qty}>Overall Qty {this.state.ascending_most_overall ? <i class="fas fa-chevron-up"></i> : <i class="fas fa-chevron-down"></i>}</th>
									</tr>
								</thead>
								<tbody>
									{most_seeling.map((medicine) => (
										<tr>
											<td>{medicine.Item_name}</td>
											<td>{medicine.times_ordered.toLocaleString(navigator.language, { minimumFractionDigits: 0 })}</td>
											<td>{medicine.overall_qty.toLocaleString(navigator.language, { minimumFractionDigits: 0 })}</td>
										</tr>
									))}
								</tbody>
							</table>
						</div>
					</div>
					<div className="col-md-4">
						<h5>Lowest Selling Medicine</h5>
						<div className="box">
							<table className="table table-bordered">
								<thead>
									<tr>
										<th>Name</th>
										<th onClick={this.ascendingtimes_ordered_never}>Times Ordered {this.state.ascending_never_times ? <i class="fas fa-chevron-up"></i> : <i class="fas fa-chevron-down"></i>}</th>
										<th onClick={this.ascendingoverall_qty_never}>Overall Qty {this.state.ascending_never_overall ? <i class="fas fa-chevron-up"></i> : <i class="fas fa-chevron-down"></i>}</th>
									</tr>
								</thead>
								<tbody>
									{lowest_selling.map((medicine) => (
										<tr>
											<td>{medicine.Item_name}</td>
											<td>{medicine.times_ordered.toLocaleString(navigator.language, { minimumFractionDigits: 0 })}</td>
											<td>{medicine.overall_qty.toLocaleString(navigator.language, { minimumFractionDigits: 0 })}</td>
										</tr>
									))}
								</tbody>
							</table>
						</div>
						<h5>Never Selling Medicine</h5>
						<div className="box">
							<table className="table table-bordered">
								<thead>
									<tr>
										<th>Name</th>
										<th >Times Ordered</th>
										<th >Overall Qty</th>
									</tr>
								</thead>
								<tbody>
									{never_selling.map((medicine) => (
										<tr>
											<td>{medicine.Item_name}</td>
											<td>{medicine.times_ordered.toLocaleString(navigator.language, { minimumFractionDigits: 0 })}</td>
											<td>{medicine.overall_qty.toLocaleString(navigator.language, { minimumFractionDigits: 0 })}</td>
										</tr>
									))}
								</tbody>
							</table>
						</div>
					</div>
				</div>
			</div>
		);
	}
}
