import React, { Component } from 'react';

import './pharmacy_data.scss';
import axios from 'axios';
import { Chart } from "react-google-charts";
import YearPicker from "react-year-picker";
import MonthPickerInput from "react-month-picker-input";
import 'react-month-picker-input/dist/react-month-picker-input.css';

export default class pharmacy_data extends Component {

	constructor(props) {
		super(props);
		this.state = {
			id: props.match.params.id,
			name: '',
			month_sales: [],
			collected_amt: [],
			medicine: [],
			selectedYear: '2020',
			selectedMonth: '2'
		};

	}

	dateChanged = (maskedValue, selectedYear, selectedMonth) => {
		axios
			.get('http://stage.medicento.com:8080/pharmacy/get_pharmacy_data/?id='+this.state.id+"&selectedYear="+selectedYear+"&selectedMonth="+(selectedMonth+1))
			.then((response) => {
				console.log(response)
				let month_sales = []
				month_sales.push(["Day", "Sales", "Order"])
				for(var i=0;i<response.data.data.length;i++) {
					console.log();
					let data=[]
					data.push(response.data.data[i].created_at__day.toString())
					data.push(parseInt(response.data.data[i].grand_total__sum))
					data.push(response.data.data[i].pk__count + " Orders")
					month_sales.push(data)
				} 
				let collected_amt = []
				collected_amt.push(["Day", "Amount", { role: 'style' }, { sourceColumn: 0, role: 'annotation', type: 'string', calc: 'stringify', }])
				for(var i=0;i<response.data.amount.length;i++) {
					console.log();
					let data=[]
					data.push(response.data.amount[i].created_at__day.toString())
					data.push(parseInt(response.data.amount[i].amount_recived__sum))
					data.push("#1989cd")
					data.push(response.data.amount[i].amount_recived__sum.toString())
					collected_amt.push(data)
				} 

				let medicine = []
				medicine.push(["Company", "qty"])
				for(var i=0;i<response.data.ord_item.length;i++) {
					console.log();
					let data=[]
					data.push(response.data.ord_item[i].manfc_name)
					data.push(parseInt(response.data.ord_item[i].quantity__sum))
					medicine.push(data)
				} 

				console.log(medicine)
				this.setState({
					month_sales: month_sales,
					collected_amt: collected_amt,
					medicine: medicine,
					name: response.data.name,
					selectedYear: selectedYear,
					selectedMonth: selectedMonth
				});
			})
			.catch((err) => {
				console.log(err);
			});
		console.log(selectedYear, selectedMonth, maskedValue);						    
	}

	componentWillMount() {
		axios
			.get('http://stage.medicento.com:8080/pharmacy/get_pharmacy_data/?id='+this.state.id)
			.then((response) => {
				console.log(response)
				let month_sales = []
				month_sales.push(["Day", "Sales", "Order"])
				for(var i=0;i<response.data.data.length;i++) {
					console.log();
					let data=[]
					data.push(response.data.data[i].created_at__day.toString())
					data.push(parseInt(response.data.data[i].grand_total__sum))
					data.push(response.data.data[i].pk__count + " Orders")
					month_sales.push(data)
				} 
				let collected_amt = []
				collected_amt.push(["Day", "Amount", { role: 'style' }, { sourceColumn: 0, role: 'annotation', type: 'string', calc: 'stringify', }])
				for(var i=0;i<response.data.amount.length;i++) {
					console.log();
					let data=[]
					data.push(response.data.amount[i].created_at__day.toString())
					data.push(parseInt(response.data.amount[i].amount_recived__sum))
					data.push("#1989cd")
					data.push(response.data.amount[i].amount_recived__sum.toString())
					collected_amt.push(data)
				} 

				let medicine = []
				medicine.push(["Company", "qty"])
				for(var i=0;i<response.data.ord_item.length;i++) {
					console.log();
					let data=[]
					data.push(response.data.ord_item[i].manfc_name)
					data.push(parseInt(response.data.ord_item[i].quantity__sum))
					medicine.push(data)
				} 

				console.log(medicine)
				this.setState({
					month_sales: month_sales,
					collected_amt: collected_amt,
					medicine: medicine,
					name: response.data.name,
				});
			})
			.catch((err) => {
				console.log(err);
			});
	}

	render() {
	
		const month_sales = this.state.month_sales;
		const collected_amt = this.state.collected_amt;
		const medicine = this.state.medicine;

		return (
			<div className="pharmacy_data">
				<h2>{this.state.name}</h2>
				<div className="row">
					<div className="col-md-12">
						<MonthPickerInput
							  year={2020}
							  month={2}
							  onChange={this.dateChanged}
						/>
					</div>
					{
						month_sales.length > 1 ? 
							<div className="col-md-6">
								<Chart
						            chartType="Bar"
						            data={ month_sales }
						            width="100%"
						            height="400px"
		        				/>	
							<h3>Month Sales</h3>
						</div>
						: <div className="col-md-6" style={{ height: '50px' }}>
							<span className="no_data">Insufficient Data For Sales {this.state.selectedYear} - {this.state.selectedMonth}
							</span>
							<h3>Month Sales</h3>
						</div>
					}
					{
						collected_amt.length > 1 ? 
							<div className="col-md-6">
								<Chart
						            chartType="Bar"
						            data={ collected_amt }
						            width="100%"
						            height="400px"
		        				/>
							<h3>Cash Collection</h3>	
						</div>
						: <div className="col-md-6">
							<span className="no_data">Insufficient Data For Cash Collection {this.state.selectedYear} - {this.state.selectedMonth}
							</span>
							<h3>Cash Collection</h3>	
						</div>
					}
					<div className="col-md-12">
						<Chart
				            chartType="PieChart"
				            data={ medicine }
				            width="100%"
				            height="400px"
		        		/>	
						<h3>Company Composition</h3>	
					</div>
				</div>
			</div>
		);
	}
}

